import { useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import SetProfile from "../../components/common/SetProfile/SetProfile";
import SaveTopBar from "../../components/TopBar/SaveTopBar/SaveTopBar";
import useUserContext from "../../hooks/useUserContext";

export default function ProfileEdit() {
  const { user, token, dispatch } = useUserContext();
  const [username, setUsername] = useState(user.username); //  
  const [accountname, setAccountname] = useState(user.accountname); //  ID
  const [intro, setIntro] = useState(user.intro); // 
  const [isPending, setIsPending] = useState(false); //  
  const uploadInp = useRef(null); //    
  const [valid, setValid] = useState(false); // 

  //   
  const navigate = useNavigate();

  //    
  const submitHandler = (event) => {
    event.preventDefault(); // submit   
    setIsPending(true); //  

    //    
    if (uploadInp.current.files[0]) {
      //      
      const files = new FormData();
      files.append("image", uploadInp.current.files[0]);

      //     
      fetch("https://mandarin.api.weniv.co.kr/image/uploadfile", {
        method: "POST",
        body: files,
      })
        .then((response) => response.json())
        .then((res) => {
          //    body   
          const body = {
            user: {
              username: username,
              accountname: accountname,
              intro: intro,
              image: `https://mandarin.api.weniv.co.kr/${res.filename}`,
            },
          };

          fetch("https://mandarin.api.weniv.co.kr/user", {
            method: "PUT",
            headers: {
              Authorization: `Bearer ${token}`,
              "Content-type": "application/json",
            },
            body: JSON.stringify(body),
          })
            .then((response) => response.json())
            .then((res) => {
              //     
              dispatch({ type: "LOGIN", payload: res.user });
            })
            .then(() => navigate("/profile", { replace: true }));
        })
        .catch((err) => {
          //   
          alert(err);
          setIsPending(false);
        });
    } else {
      //        
      const body = {
        user: {
          username: username,
          accountname: accountname,
          intro: intro,
          image: user.image,
        },
      };

      fetch("https://mandarin.api.weniv.co.kr/user", {
        method: "PUT",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-type": "application/json",
        },
        body: JSON.stringify(body),
      })
        .then((response) => response.json())
        .then((res) => {
          //     
          dispatch({ type: "LOGIN", payload: res.user });
        })
        .then(() => {
          navigate("/profile", { replace: true }); //     
        })
        .catch((err) => {
          //   
          alert(err);
          setIsPending(false);
        });
    }
  };

  return (
    <>
      <SaveTopBar handler={submitHandler} isPending={isPending} valid={valid} />
      <SetProfile
        join={false}
        username={username}
        accountname={accountname}
        intro={intro}
        uploadInp={uploadInp}
        setUsername={setUsername}
        setAccountname={setAccountname}
        setIntro={setIntro}
        setValid={setValid}
      />
    </>
  );
}
