import { useEffect, useRef, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import ProductInput from "../../components/ProductInput/ProductInput";
import SaveTopBar from "../../components/TopBar/SaveTopBar/SaveTopBar";
import useUserContext from "../../hooks/useUserContext";
import { Section, ImageLabel, Preview, UploadLabel } from "./styledProduct";

export default function ProductEdit() {
  const { token } = useUserContext();
  const param = useParams();

  const [preview, setPreview] = useState(null); //  
  const [itemName, setItemName] = useState(""); // 
  const [itemPrice, setItemPrice] = useState(""); // 
  const [itemLink, setItemLink] = useState(""); //  
  const [isPending, setIsPending] = useState(false); //  
  const [valid, setValid] = useState(true); //   

  const uploadInp = useRef(); //    

  //   
  const navigate = useNavigate();

  const itemNamehandler = (event) => {
    //    
    setItemName(event.target.value);
  };
  const itemPricehandler = (event) => {
    //    
    const test = new RegExp("^[\\d]+$", "g");
    if (test.test(event.target.value)) setItemPrice(event.target.value);
    else return;
  };
  const itemLinkhandler = (event) => {
    //     
    setItemLink(event.target.value);
  };

  const uploadHandler = (event) => {
    //    null
    if (!event.target.files[0]) setPreview(null);
    else {
      //  FileReader API  
      const reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]);

      return new Promise((resolve) => {
        reader.onload = () => {
          setPreview(reader.result);
          resolve();
        };
      });
    }
  };

  const submitHandler = (event) => {
    event.preventDefault(); // submit   
    setIsPending(true); //  

    //    
    if (uploadInp.current.files[0]) {
      //      
      const files = new FormData();
      files.append("image", uploadInp.current.files[0]);

      //     
      fetch("https://mandarin.api.weniv.co.kr/image/uploadfile", {
        method: "POST",
        body: files,
      })
        .then((response) => response.json())
        .then((res) => {
          //    body   
          const body = {
            product: {
              itemName: itemName,
              price: itemPrice,
              link: itemLink,
              itemImage: `https://mandarin.api.weniv.co.kr/${res.filename}`,
            },
          };

          fetch(`https://mandarin.api.weniv.co.kr/product/${param.productid}`, {
            method: "PUT",
            headers: {
              Authorization: `Bearer ${token}`,
              "Content-type": "application/json",
            },
            body: JSON.stringify(body),
          })
            .then((response) => response.json())
            .then(() => {
              navigate("/profile", { replace: true }); //     
            });
        })
        .catch((err) => {
          //   
          alert(err);
          setIsPending(false);
        });
    } else {
      //        
      const body = {
        product: {
          itemName: itemName,
          price: itemPrice,
          link: itemLink,
          image: preview,
        },
      };

      fetch(`https://mandarin.api.weniv.co.kr/product/${param.productid}`, {
        method: "PUT",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-type": "application/json",
        },
        body: JSON.stringify(body),
      })
        .then((response) => response.json())
        .then(() => navigate("/profile", { replace: true })) //     
        .catch((err) => {
          //   
          alert(err);
          setIsPending(false);
        });
    }
  };

  //   
  useEffect(() => {
    if (
      preview &&
      itemName.length > 0 &&
      itemName.length < 16 &&
      itemPrice.length &&
      itemLink.length > 0
    ) {
      setValid(true);
    } else {
      setValid(false);
    }
  }, [preview, itemName, itemPrice, itemLink]);

  //        
  useEffect(() => {
    fetch(
      `https://mandarin.api.weniv.co.kr/product/detail/${param.productid}`,
      {
        method: "GET",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-type": "application/json",
        },
      }
    )
      .then((res) => res.json())
      .then((res) => {
        setPreview(res.product.itemImage);
        setItemName(res.product.itemName);
        setItemPrice("" + res.product.price);
        setItemLink(res.product.link);
        setValid(true);
      });
  }, []);

  return (
    <>
      <SaveTopBar handler={submitHandler} isPending={isPending} valid={valid} />
      <Section>
        <ImageLabel> </ImageLabel>
        <Preview>
          {preview ? <img src={preview} /> : ""}
          <UploadLabel>
            <input
              ref={uploadInp}
              type="file"
              accept="image/*"
              onChange={uploadHandler}
              style={{ display: "none" }}
            />
          </UploadLabel>
        </Preview>

        <ProductInput
          label=""
          placeholder="1~15  ."
          stateInp={itemName}
          handler={itemNamehandler}
          inptype="text"
        />
        <ProductInput
          label=""
          placeholder="  ."
          stateInp={itemPrice}
          handler={itemPricehandler}
          inptype="number"
        />
        <ProductInput
          label=" "
          placeholder="URL  ."
          stateInp={itemLink}
          handler={itemLinkhandler}
          inptype="text"
        />
      </Section>
    </>
  );
}
