import { useNavigate } from "react-router-dom";
import useUserContext from "../../../../hooks/useUserContext";
import { AlertWrapper } from "./styledDeleteAlert";

const LogOutAlert = (props) => {
  const { dispatch } = useUserContext();
  const navigate = useNavigate();

  function handleCancelLogOut() {
    props.setIsLogOut(false);
  }

  function handleLogOut() {
    dispatch({ type: "LOGOUT" }); // user null
    localStorage.removeItem("token"); //  
    navigate("/"); //  
  }

  return (
    <>
      <AlertWrapper>
        <h3 className="hidden">  </h3>
        <div>
          <strong>?</strong>
          <ul>
            <li>
              <button onClick={handleCancelLogOut}></button>
            </li>
            <li>
              <button className="alert-del" onClick={handleLogOut}>
                
              </button>
            </li>
          </ul>
        </div>
      </AlertWrapper>
    </>
  );
};
export default LogOutAlert;
