import { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import ProductInput from "../../components/ProductInput/ProductInput";
import SaveTopBar from "../../components/TopBar/SaveTopBar/SaveTopBar";
import useUserContext from "../../hooks/useUserContext";
import { Section, ImageLabel, Preview, UploadLabel } from "./styledProduct";

export default function Product() {
  const { token } = useUserContext();
  const [preview, setPreview] = useState(null); //  
  const [itemName, setItemName] = useState(""); // 
  const [itemPrice, setItemPrice] = useState(""); // 
  const [itemLink, setItemLink] = useState(""); //  
  const [isPending, setIsPending] = useState(false); //  
  const [valid, setValid] = useState(false); //   
  const uploadInp = useRef(); //    
  const navigate = useNavigate();

  const itemNamehandler = (event) => {
    //    
    setItemName(event.target.value);
  };

  const itemPricehandler = (event) => {
    const value = event.target.value;
    //    
    const test = new RegExp("^[0-9]+$", "g");
    if (value === "") setItemPrice("");
    else if (test.test(value)) setItemPrice(value);
    else return;
  };

  const itemLinkhandler = (event) => {
    //     
    setItemLink(event.target.value);
  };

  const uploadHandler = (event) => {
    //    null
    if (!event.target.files[0]) setPreview(null);
    else {
      //  FileReader API  
      const reader = new FileReader();

      reader.readAsDataURL(event.target.files[0]);

      return new Promise((resolve) => {
        reader.onload = () => {
          setPreview(reader.result);
          resolve();
        };
      });
    }
  };

  const submitHandler = (event) => {
    if (+itemPrice === 0) {
      alert(" 1  !");
      return;
    }
    event.preventDefault(); // submit    
    setIsPending(true); //  

    //   FormData  
    const files = new FormData();
    files.append("image", uploadInp.current.files[0]);

    fetch("https://mandarin.api.weniv.co.kr/image/uploadfile", {
      method: "POST",
      body: files,
    })
      .then((response) => response.json())
      .then((res) => {
        //      

        //        API 
        const body = {
          product: {
            itemName: itemName,
            price: +itemPrice,
            link: itemLink,
            itemImage: `https://mandarin.api.weniv.co.kr/${res.filename}`,
          },
        };

        fetch("https://mandarin.api.weniv.co.kr/product", {
          method: "POST",
          headers: {
            Authorization: `Bearer ${token}`,
            "Content-type": "application/json",
          },
          body: JSON.stringify(body),
        })
          .then((response) => response.json())
          .then(() => navigate("/profile", { replace: true }))
          .catch((err) => console.log(err));
      })
      .catch((err) => {
        //   
        alert(err);
        setIsPending(false);
      });
  };

  useEffect(() => {
    if (
      preview &&
      itemName.length > 0 &&
      itemName.length < 16 &&
      itemPrice.length > 0 &&
      itemLink.length > 0
    ) {
      setValid(true);
    } else {
      setValid(false);
    }
  }, [preview, itemName, itemPrice, itemLink]);

  return (
    <>
      <SaveTopBar
        handler={submitHandler}
        move="/profile"
        isPending={isPending}
        valid={valid}
      />
      <Section>
        <ImageLabel> </ImageLabel>
        <Preview>
          {preview ? <img src={preview} /> : ""}
          <UploadLabel>
            <input
              ref={uploadInp}
              type="file"
              accept="image/*"
              onChange={uploadHandler}
              style={{ display: "none" }}
            />
          </UploadLabel>
        </Preview>
        <p>{preview ? null : " ."}</p>
        <ProductInput
          label=""
          placeholder="1~15  ."
          stateInp={itemName}
          handler={itemNamehandler}
          inptype="text"
          min="2"
          max="15"
        />
        <ProductInput
          label=""
          placeholder="  ."
          stateInp={itemPrice}
          handler={itemPricehandler}
          inptype="text"
        />
        <ProductInput
          label=" "
          placeholder="URL  ."
          stateInp={itemLink}
          handler={itemLinkhandler}
          inptype="text"
        />
      </Section>
    </>
  );
}
