import {
  Wrapper,
  ProfileModifyButton,
  RegisterItem,
  CircleButton,
  FollowButton,
} from "./styledProfileFooter";
import messageIcon from "../../../../assets/images/icon-message-circle.png";
import shareIcon from "../../../../assets/images/icon-share.png";
import { useState } from "react";
import useUserContext from "../../../../hooks/useUserContext";

export default function ProfileFooter({ isMine, setUserProfile, data }) {
  const { token } = useUserContext();
  const [isPending, setIsPending] = useState(false);

  const chatHandler = (event) => {
    alert(
      "     .\n    ."
    );
  };

  const followHandler = (event) => {
    switch (event.target.textContent) {
      case "":
        setIsPending(true); //  
        fetch(
          `https://mandarin.api.weniv.co.kr/profile/${data.accountname}/unfollow`,
          {
            method: "DELETE",
            headers: {
              Authorization: `Bearer ${token}`,
              "Content-type": "application/json",
            },
          }
        )
          .then((res) => res.json())
          .then((res) => {
            setUserProfile(res);
            setIsPending(false); //  
          })
          .catch((err) => {
            console.log(err);
            setIsPending(false); //  
          });
        break;
      case "":
        setIsPending(true); //  
        fetch(
          `https://mandarin.api.weniv.co.kr/profile/${data.accountname}/follow`,
          {
            method: "POST",
            headers: {
              Authorization: `Bearer ${token}`,
              "Content-type": "application/json",
            },
          }
        )
          .then((res) => res.json())
          .then((res) => {
            setUserProfile(res);
            setIsPending(false); //  
          })
          .catch((err) => {
            console.log(err);
            setIsPending(false); //  
          });
        break;
      default:
        console.log(",    ");
        break;
    }
  };

  const shareHandler = (event) => {
    navigator.clipboard.writeText(window.location.href);
    alert(" .  !");
  };

  return (
    <Wrapper>
      {isMine ? (
        <>
          <ProfileModifyButton to="/profile/edit">
             
          </ProfileModifyButton>
          <RegisterItem to="/product"> </RegisterItem>
        </>
      ) : (
        <>
          <CircleButton onClick={chatHandler}>
            <img src={messageIcon} />
          </CircleButton>
          <FollowButton
            onClick={followHandler}
            bgcolor={data.isfollow}
            disabled={!data.accountname && isPending}
          >
            {data.isfollow ? "" : ""}
          </FollowButton>
          <CircleButton onClick={shareHandler}>
            <img src={shareIcon} />
          </CircleButton>
        </>
      )}
    </Wrapper>
  );
}
